import React from 'react'
import { toast } from "react-toastify";
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import ProgressBar from 'react-bootstrap/ProgressBar';
import BottomBackNextBtn from 'views/mobileView/components/atoms/BottomBackNextBtn';
import db from 'libs/db';
import { AiFillCheckCircle, AiFillCloseCircle } from 'react-icons/ai';
import AdminServices from 'services/api/AdminServices';
import { panValidator } from 'libs/validators';
import cssVar from 'views/mobileView/styles';
import ActivityLoaderMintbox from 'views/mobileView/components/atoms/ActivityLoaderMintbox';

const PanScreen = (props) => {
    const taxStatus = props.location.state?.taxStatus

	const [pan, setPan] = React.useState('');
	const [invalidPan, setInvalidPan] = React.useState(false)
	const [nameOnPan, setNameOnPan] = React.useState('');
	const [kycRegistered, setKycRegistered] = React.useState(0);
	const [nextDisabled, setNextDisabled] = React.useState(true);
	const [loader, setloader] = React.useState(false);

	React.useEffect(()=>{
		if(!props.location.state?.taxStatus){
			props.history.push('/activate-direct-mf/tax-status')
		} else {
			db.get(db.model.userData).then((u) => {
				if (u) {
					if (u.panData) {
						setPan(u.panData.pan || '')
					}
				}
			})
		}
	}, []);

	React.useEffect(() => {
		setNextDisabled(true);
		if (pan.length === 10) {
			if (panValidator(pan).isValid) {
				setInvalidPan(false)
				checkKYC(pan)
			}
			else setInvalidPan(true)
		}
	}, [pan]);

	const checkKYC = async (pan) => {
		setloader(true);
		try {
			const res = await AdminServices.checkkyc({pan}).then((res) => res);
			console.log("checkkyc0", res)
			if (res.data.f_PAN_NAME !== '' && res.data.f_PAN_STATUS === 'OK') {
				setNameOnPan(res.data.f_PAN_NAME);
				setKycRegistered(1)
			} else {
				setNameOnPan('');
				setKycRegistered(0)
			}
			setNextDisabled(false)
			setloader(false);
		} catch (error) {
			toast.error("Try again ...");
			setloader(false);
		}
	};

	return (
		<div className='mainContainer'>
			<MainHeader
				header="Activate mutual funds"
				navigation={props.history}
			/>
			<ProgressBar now={20} className="progress-bar-custom" />
			<div className="dataContainer" style={{paddingTop:'42px'}}>
				<div className='bodyTextDark'>Enter your PAN</div>
				<input
					className="normalInput"
					placeholder="ABCD1234E"
					autoComplete='off'
					autoFocus
                    style={{textTransform:'uppercase'}}
					maxLength={10}
					id="pan"
					type="text"
					onChange={(e) => {
						setPan(e.target.value);
					}}
					value={pan}
				/>
				{
					invalidPan ? <div className='errorMsg'>Invalid Pan. Try again!</div> : ''
				}
				{
					pan.length === 10 && !invalidPan ?
						<div>
							{loader ?
								<center>
									<ActivityLoaderMintbox />
									<div className='bodyTextLight'>Fetching your KYC details...Please wait!
									</div>
								</center>
								:
								kycRegistered === 1 ?
									<>
										<div className='bodyTextLight' style={{marginTop:'8%'}}>Name: <span className='bodyTextLight'>{nameOnPan}</span></div>
										<div style={{marginTop:'4%'}}>
										<AiFillCheckCircle className='secondaryGreen' size={20} /><span className='header2dark'> You are KYC registered</span>
										</div>
									</>
									:
									<>
										<div className='header2dark' style={{marginTop:'8%'}}>
											<AiFillCloseCircle color={cssVar.getPropertyValue('--red')} size={20} /> You are not KYC registered
										</div>
											<div className='bodyTextLight' style={{ marginTop: "4%" }}>Don’t worry, complete your account and we will help you get KYC registered online.</div>
									</>
							}
						</div>
						:
						""
				}
			</div>
			<div className='footer'>
				<BottomBackNextBtn
					nextDisabled={nextDisabled}
					navigation={props.history}
					buttonText="Confirm"
					next="/activate-direct-mf/name"
					params={{ ...props.location.state, nameOnPan}}
					saveData={{
						panData: {
							pan,
							nameOnPan,
							kycRegistered
						}
					}} />
			</div>
		</div>
	)
}

export default PanScreen
